import React from 'react'
import { createContainer } from '../../../src'

class Hello extends React.Component { // a little worker
  static propTypes = {
    hello: React.PropTypes.string,
    name: React.PropTypes.string,
    intents: React.PropTypes.object
  }
  render() {
    return (
      <div>
        <div>{this.props.hello} {this.props.name}</div>
        <div><input onChange={e => this.props.intents.updateName$.next(e.target.value)} /></div>
      </div>
    )
  }
}

export default createContainer(Hello, (intents, model) => { // just like a manager
  const fragments = {
    hello: null,
    name: null
  }
  const updateName$ = intents.get('updateName')
  updateName$.
    subscribe(name => model.update({ name: name }))

  return {
    fragments,
    updateName$
  }
})



